.site-overflow {
  z-index: 1;
  cursor: pointer;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: #ffffff;
  opacity: 70%;
  left: 0;
}

.cart {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: fixed;
  height: 100vh;
  width: 760px;
  background-color: #000000;
  color: #ffffff;
  font-size: 24px;
  font-weight: normal;
  padding: 2rem 4.6rem;
  letter-spacing: 0.01em;
  z-index: 3;
  overflow-y: auto;
}
.cart .cart-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 50px;
}
.cart .product-in-cart {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 281px;
  margin-bottom: 2rem;
}
.cart .product-in-cart__prod-img {
  width: 281px;
  height: 281px;
  display: block;
  background-color: #ffffff;
  -webkit-box-sizing: initial;
          box-sizing: initial;
  margin-bottom: 1rem;
  -o-object-fit: contain;
     object-fit: contain;
  -o-object-position: top;
     object-position: top;
}

.close {
  cursor: pointer;
}

.cart-title {
  display: block;
  border-bottom: 1px solid #FCFBF6;
  padding-bottom: 10px;
}

.cart-prod-list {
  overflow-y: scroll;
}

.product-in-cart > span {
  -webkit-box-sizing: initial;
          box-sizing: initial;
  margin-bottom: 1rem;
}

.product-in-cart__amount {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.product-in-cart__amount-button {
  display: inline-block;
  background-color: #000;
  color: #FCFBF6;
  font-size: 1.5rem;
  height: 35px;
  width: 35px;
  text-align: center;
  cursor: pointer;
  border: none;
}

.product-in-cart__amount-text {
  margin: 1rem;
}

.order-summary {
  display: block;
  width: 220px;
  height: 80px;
  background-color: #FCFBF6;
  color: #000;
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  margin: 20px auto 0;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .site-overflow {
    display: none;
  }

  .cart {
    width: 100vw;
  }
}



